body,
html {
  overflow-x: hidden;
  background: #FFF;
  color: #1D1D1B;
  font-family: "Gtwall", sans-serif;
  font-weight: 300; }

/*==========================NOVO=============================*/
:focus {
  outline: none; }

::selection {
  background: #1D1D1B;
  color: #FFF;
  text-shadow: none; }

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #F9F9F8;
  /*text-shadow: 1px 1px 1px #080707;*/ }

input::-moz-placeholder,
textarea::-moz-placeholder {
  color: #F9F9F8;
  /*text-shadow: 1px 1px 1px #080707;*/ }

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #F9F9F8;
  /*text-shadow: 1px 1px 1px #080707;*/ }

input::placeholder,
textarea::placeholder {
  color: #F9F9F8;
  /*text-shadow: 1px 1px 1px #080707;*/ }

.uppercase {
  text-transform: uppercase; }

hr {
  border-bottom: 1px solid white;
  width: 100%; }

[type='text'],
[type='password'],
[type='date'],
[type='datetime'],
[type='datetime-local'],
[type='month'],
[type='week'],
[type='email'],
[type='number'],
[type='search'],
[type='tel'],
[type='time'],
[type='url'],
[type='color'],
textarea {
  border-color: white;
  background-color: #FFF;
  color: #828282;
  text-shadow: 1px 1px 1px white; }

[type='text']:focus,
[type='password']:focus,
[type='date']:focus,
[type='datetime']:focus,
[type='datetime-local']:focus,
[type='month']:focus,
[type='week']:focus,
[type='email']:focus,
[type='number']:focus,
[type='search']:focus,
[type='tel']:focus,
[type='time']:focus,
[type='url']:focus,
[type='color']:focus,
textarea:focus {
  border-color: transparent;
  background-color: #FFF;
  box-shadow: inset 2px 2px 8px #d9d9d9; }

p {
  font-weight: 300;
  line-height: 170%;
  font-size: 16px;
  margin-bottom: 25px; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Gtwall", sans-serif;
