.button {
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 0;
  border: 0;
  margin-top: 2vw;
  width: auto;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  background-color: #FFF;
  height: 50px;
  border-bottom: 1px solid white; }

.navButton {
  background-color: #1D1D1B;
  color: #FFED00;
  height: 50px;
  width: 50px;
  font-size: 1.2rem;
  margin-left: -0.987rem;
  display: flex;
  align-items: center;
  justify-content: center; }

.menuHeader {
  font-family: "Gtwall", sans-serif;
  font-weight: 400;
  text-transform: uppercase; }
  .menuHeader li {
    border-left: 1px solid white; }
    .menuHeader li a {
      height: 50px;
      display: flex;
      align-items: center;
      color: #000; }
      .menuHeader li a:hover, .menuHeader li a:focus, .menuHeader li a.active {
        background: #F9F9F8; }

.menuFazemos li a {
  padding: 0; }
  .menuFazemos li a h1 {
    margin: 0;
    font-weight: 700;
    transition: 0.3s all ease-in-out; }
  .menuFazemos li a:hover h1 {
    color: #FFF; }
  .menuFazemos li a:hover ~ a h1 {
    color: rgba(255, 255, 255, 0.3); }

.bannerContainer {
  height: calc(100vh - 65px);
  background-color: #1D1D1B; }

section {
  overflow: hidden;
  transition: 0.3s background-color ease-in-out; }
  section.space-section {
    padding-top: 10vh;
    padding-bottom: 10vh; }

.bodyContainer {
  background: #F9F9F8;
  min-height: 50vh;
  margin-top: 50px; }

.title-big {
  font-weight: 700;
  font-size: 54px;
  line-height: 1; }

.breadcrumbsContainer .breadcrumbs {
  margin: 0; }
  .breadcrumbsContainer .breadcrumbs li {
    color: #1D1D1B;
    font-weight: 700; }
    .breadcrumbsContainer .breadcrumbs li:after {
      color: #1D1D1B;
      content: "|"; }
  .breadcrumbsContainer .breadcrumbs a, .breadcrumbsContainer .breadcrumbs .disabled {
    color: #1D1D1B;
    font-weight: 400; }

.bg-dark .breadcrumbsContainer .breadcrumbs {
  margin: 0;
  color: #F9F9F8; }
  .bg-dark .breadcrumbsContainer .breadcrumbs li {
    color: #F9F9F8;
    font-weight: 700; }
    .bg-dark .breadcrumbsContainer .breadcrumbs li:not(:last-child)::after {
      color: #F9F9F8;
      font-weight: 700; }
  .bg-dark .breadcrumbsContainer .breadcrumbs a, .bg-dark .breadcrumbsContainer .breadcrumbs .disabled {
    color: #F9F9F8;
    font-weight: 400; }

.portfolio-cover {
  overflow: hidden; }

.portfolio-foto {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-size: cover;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  width: calc(100% + 50px);
  transition: opacity .4s, transform .4s;
  transform: translate3d(-30px, 0, 0); }

.caseDescricao h2 {
  font-size: 1.8rem; }

.caseDescricao h3 {
  font-size: 1.4rem; }

.caseObjetivo .title-big {
  font-size: 1.4rem; }

#blogHome .noticia {
  width: 100%; }

.pagination-area .pagination li, .pagination-area .pagination li a {
  color: #1D1D1B;
  font-weight: bold; }
  .pagination-area .pagination li:hover, .pagination-area .pagination li a:hover {
    background: none;
    color: #F9F9F8; }
  .pagination-area .pagination li.current, .pagination-area .pagination li a.current {
    background: none;
    color: #F9F9F8; }
